import { useRef, useState } from "react";
import { Modal, Button, Form } from "react-bootstrap";
import { payoutDoTransaction } from "../../../../api/login/Login";
import './AepsPinModalNew.css';
import withLocation from "../../../../utils/withLocation";

function AepsPinModalNew(props) {
  const length = 4;
  const [otp, setOtp] = useState(new Array(length).fill(''));
  const inputs = useRef([]);

  const otpHandleChange = (index, value) => {
    if (value.length > 1) return;
    const newOtp = [...otp];
    newOtp[index] = value;
    setOtp(newOtp);

    if (value && inputs.current[index + 1]) {
      inputs.current[index + 1].focus();
    }
  };

  const handleKeyDown = (index, e) => {
    if (e.key === 'Backspace' && !otp[index] && inputs.current[index - 1]) {
      inputs.current[index - 1].focus();
    }
  };

const submitOtp = withLocation(async (latitude, longitude) => {
  const tpin = otp.join('');
  const datasend = { 
    ...props?.initialValue, 
    user_id: window.localStorage.getItem('userIdToken'), 
    tpin: tpin, 
    latlong: `${latitude},${longitude}`, 
    remark :"payout",
    benificiaryId: props?.selectedItem?._id,
  };
 
  try {
    const res = await payoutDoTransaction(datasend);
    if (res?.data?.error === false) {
      props.toastSuccessMessage(res?.data?.message || 'Transaction Processed');
      setTimeout(() => {
        props.onHide();
      }, 1000);
    } else {
      props.toastErrorMessage(res?.data?.message || 'Transaction Failed');
    }
  } catch (error) {
    props.toastErrorMessage('An error occurred');
  } finally {
     props.setLoading(false)
  }

  setOtp(new Array(length).fill(''));
});


const transactionProcess = async ()=> {
     props.setLoading(true)
      await submitOtp()
       props.setLoading(false)
}


  return (
    <Modal
      {...props}
      centered
      className="aeps-pin-modal"
      style={{ zIndex: 1050 }}
    >
      <Modal.Header closeButton>
        <Modal.Title>Move to Bank</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div className="transaction-details">
          <h4>Transaction Details</h4>
          <div className="detail-row">
            <span>Amount:</span>
            <strong>â‚¹ {props?.initialValue?.amount}</strong>
          </div>
          <div className="detail-row">
            <span>Bank:</span>
            <strong>{props?.initialValue?.bank_name}</strong>
          </div>
          <div className="detail-row">
            <span>Account:</span>
            <strong>{props?.initialValue?.account_number}</strong>
          </div>
          <div className="detail-row">
            <span>Payment Mode:</span>
            <strong>{props?.initialValue?.paymentMode === 'neft' ? 'NEFT' : 'IMPS'}</strong>
          </div>
        </div>

        <Form.Group className="mt-4">
          <Form.Label>Enter TPIN</Form.Label>
          <div className="otp-input-group">
            {otp.map((value, index) => (
              <Form.Control
                key={index}
                type="password"
                maxLength="1"
                value={value}
                onChange={(e) => otpHandleChange(index, e.target.value)}
                onKeyDown={(e) => handleKeyDown(index, e)}
                ref={(input) => (inputs.current[index] = input)}
              />
            ))}
          </div>
        </Form.Group>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={props.onHide}>
          Cancel
        </Button>
        <Button 
          variant="primary" 
          onClick={transactionProcess} 
          disabled={otp.join('').length !== 4}
        >
          Confirm Payment
        </Button>
      </Modal.Footer>
    </Modal>
  );
}

export default AepsPinModalNew;
