import { Link } from "react-router-dom"
import Loader from "../../../common/loader/Loader"
import { useEffect, useState } from "react";
import { bankListApi, payoutAccountEnquiry, payoutBankDelete, payoutBenefiaries, userValidate } from "../../../api/login/Login";
import { MdDelete } from "react-icons/md";
import AepsPinModal from "./aepsPinModal/AepsPinModal";
import {  toast } from "react-toastify";
import { Popconfirm } from 'antd';
import { Modal, Button } from 'react-bootstrap';
import AepsPinModalNew from "./aepsPinModal/AepsPinModalNew";
import AddAepsPayout from "./addAepsPayout/AddAepsPayout";
import "./AepsPayout.css"
import {
  Box,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper
} from "@mui/material";

function AepsPayout() {
    const [loading, setLoading] = useState(false);
    const [modalShow, setModalShow] = useState(false);
    const [showForm, setShowForm] = useState(false);
    const [data, setData] = useState(null)
    // console.log(data);
    const [walletData, setWalletData] = useState(null)
    const [selectedItem, setSelectedItem] = useState(null);
    
    const [amountError, setAmountError] = useState('');
    const [showAddPayoutModal, setShowAddPayoutModal] = useState(false);
    console.log(selectedItem);

    const [initialValue, setInitialValue] = useState({
        
        amount: '',
        
        paymentMode: 'neft',
        tpin: '',
        bank_name: '',
        account_holder_name: '',
        ifsc_code: '',
        mobile_number: '',
        account_number: '',
        // status: ''
    })

    
    const handlePayButtonClick = (item) => {
        setSelectedItem(item);
        setInitialValue({
            ...initialValue,
            paymentMode: 'neft',
            bank_name: item.bank_name,
            account_holder_name: item.bank_account_name,
            ifsc_code: item.bank_ifsc,
            mobile_number: item.mobile_number,
            account_number: item.bank_account_number
        });
        setShowForm(true);
    }

    // console.log(initialValue);
    const validateAmount = (value) => {
        const numValue = Number(value);
        if (isNaN(numValue) || numValue < 100 || numValue > 100000) {
            setAmountError('Amount must be between 100 and 100,000');
        } else {
            setAmountError('');
        }
    }

    const handleChangeSet = (e) => {
        const clone = { ...initialValue }
        const value = e.target.value
        const name = e.target.name
        clone[name] = value
        setInitialValue(clone)
    
        if (name === 'amount') {
            validateAmount(value);
        }
    }

    const aepsPayoutList = async () => {
        setLoading(true);
        try {
            const res = await payoutBenefiaries();
            // Check if res?.data?.data is an array, otherwise set an empty array
            if (Array.isArray(res?.data?.data)) {
                setData(res?.data?.data);
            } else {
                setData([]); // Set an empty array to avoid map error
            }
            setLoading(false);
        } catch (error) {
            setLoading(false);
            console.error("Error fetching payout beneficiaries:", error);
            setData([]); // Ensure data is at least an empty array on error
        }
    };
    

    const toastSuccessMessage = (message) => {
        toast.success(`${message}`, {
            position: "top-center",
            autoClose: 2000,
        });
    };
    const toastErrorMessage = (message) => {
        toast.error(`${message}`, {
            position: "top-center",
            autoClose: 2000,
        });
    };
const handleopenForm=()=>{
    setShowForm(true);
}
const handleCloseForm=()=>{
    setShowForm(false);
    setInitialValue('');
}
    const enquiryStatus = async (id) => {
        try {
            const res = await payoutAccountEnquiry(id)
            // console.log(res?.data);
            if (res?.data?.error == false) {
                toastSuccessMessage(res?.data?.message)
                aepsPayoutList()
            } else {
                toastErrorMessage(res?.data?.message)
            }
        } catch (error) {

        }
    }

    const deletePayoutDelete = async (id) => {
        try {
            const res = await payoutBankDelete(id)
            if (res?.data?.error == false) {
                toastSuccessMessage(res?.data?.message)
                aepsPayoutList()
            } else {
                toastErrorMessage(res?.message)
            }
        } catch (error) {

        }
    }
    const confirm = (id) => {
        console.log(id);
        deletePayoutDelete(id)
    }

    const changeRadioObject = (e, item) => {
        setSelectedItem(item);
    }

    useEffect(() => {
        if (data && data.length > 0) {
            setSelectedItem(data[0]); // Select the first item by default
        }
    }, [data]);

    const aepsWallet = async () => {
        try {
            const res = await userValidate()
            setWalletData(res?.data)
        } catch (error) {

        }
    }

    const handleChangeChanel = (str) => {
        const clone = { ...initialValue, paymentMode: str }
        setInitialValue(clone);
    }

    const handleSumitPayemt = () => {
        const clone = { ...initialValue }
        // console.log(clone);
    }

 

    useEffect(() => {
        aepsPayoutList()
        aepsWallet()
     
    }, [])

    return (
        <>
            {loading && <Loader />}
            <div className="PageHeading">
                <h1>AEPS Settlement Details</h1>
            </div>

            <div className="ContentArea">
                <div className="card">
                    <div className="card-header">AEPS Bank Details<span style={{ color: "var(--bs-primary2)"}}> <Button onClick={() => setShowAddPayoutModal(true)} class="btn  btn-secondary" style={{ backgroundColor: "var(--bs-primary2)" }}>Add Payout Account</Button> </span></div>
                    <div className="card-body">
                         <Box sx={{ my: 2 }}>
      <TableContainer component={Paper} elevation={3}>
        <Table>
          <TableHead sx={{ backgroundColor: "#f5f5f5" }}>
            <TableRow>
              <TableCell><strong>S.No.</strong></TableCell>
              <TableCell sx={{minWidth:'180px'}}><strong>Account Holder Name</strong></TableCell>
              <TableCell><strong>Mobile No</strong></TableCell>
              <TableCell><strong>Account No</strong></TableCell>
              <TableCell sx={{minWidth:'180px'}}><strong>Bank</strong></TableCell>
              <TableCell><strong>IFSC Code</strong></TableCell>
              <TableCell><strong>Current Status</strong></TableCell>
              <TableCell><strong>Action</strong></TableCell>
              <TableCell sx={{minWidth:'180px'}}><strong>Move to Bank</strong></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {Array.isArray(data) && data.length > 0 ? (
              data.map((item, index) => (
                <TableRow key={item?._id} hover>
                  <TableCell sx={{ color: "var(--bs-primary2)" }}>{(index + 1)}</TableCell>
                  <TableCell sx={{ color: "var(--bs-primary2)" }}>{item?.bank_account_name}</TableCell>
                  <TableCell sx={{ color: "var(--bs-primary2)" }}>{item?.mobile_number}</TableCell>
                  <TableCell sx={{ color: "var(--bs-primary2)" }}>{item?.bank_account_number}</TableCell>
                  <TableCell sx={{ color: "var(--bs-primary2)" }}>{item?.bank_name}</TableCell>
                  <TableCell sx={{ color: "var(--bs-primary2)" }}>{item?.bank_ifsc}</TableCell>

                  <TableCell>
                    {item?.status === 1 && (
                      <button type="button"  style={{minWidth:'150px'}} className="btn btn-warning" onClick={() => enquiryStatus(item?._id)}>
                            Pending
                        </button>
                    )}
                    {item?.status === 2 && (
                        <button type="button"  style={{minWidth:'150px'}} className="btn btn-success" disabled
                        // onClick={() => enquiryStatus(item?._id)}
                        >
                            Approved
                        </button>
                    )}
                    {item?.status === 3 && (
                         <button type="button" style={{minWidth:'150px'}} disabled className="btn btn-danger" 
                        // onClick={() => enquiryStatus(item?._id)}
                        >
                            Failed
                        </button>
                    )}
                  </TableCell>

                  <TableCell>
                    <Popconfirm
                      title="Delete"
                      description="Are you sure to delete?"
                      okText="Yes"
                      cancelText="No"
                      onConfirm={() => confirm(item?._id)}
                      okButtonProps={{
                        className: "popoverconfirm",
                        style: {
                          backgroundColor: "var(--bs-primary1)",
                          borderColor: "var(--bs-primary1)"
                        }
                      }}
                    >
                      <MdDelete style={{ cursor: "pointer", fontSize: "20px" }} />
                    </Popconfirm>
                  </TableCell>

                  <TableCell>
                    <button
                        type="button"
                        onClick={() => handlePayButtonClick(item)}
                        className={`btn btn-secondary ${item?.status !== 2 ? 'disabled' : ''}`}
                    >
                        Move to Bank
                    </button>
                  </TableCell>
                </TableRow>
              ))
            ) : (
              <TableRow>
                <TableCell colSpan={9} align="center" sx={{ color:"var(--bs-primary2)" }}>
                  No payout beneficiaries found.
                </TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </TableContainer>
    </Box>
                        <Modal show={showForm} onHide={handleCloseForm} size="lg" tyle={{
            zIndex: 1050,
           
          }} >
      <Modal.Header closeButton>
        <Modal.Title>Settled Details</Modal.Title>
      </Modal.Header>
      <Modal.Body style={{
    backgroundColor: '#ffffff',
    transform: 'translateZ(0)',
    backfaceVisibility: 'hidden',
  }}>
                        <form action="" method="post" name="frmReport" id="frmReport">
                            <input type="hidden" id="hidID" name="hidID" />
                            <div className="form-row" style={{ alignItems: 'end' }}>
                                {/* <div className="form-group col-md-4">
                                    <label htmlFor="txtUserId">Min Amount</label>
                                    <input type="number" name="min_amt" id="account_no" className="form-control" value={filterInitial.min_amt} onChange={handleChange} />
                                </div>
                                <div className="form-group col-md-4">
                                    <label htmlFor="txtUserId">Max Amount</label>

                                    <input type="number" name="max_amt" id="account_no" className="form-control" value={filterInitial.max_amt} onChange={handleChange} />
                                </div> */}

                                <div className="form-group col-sm-12 col-md-4">
                                    <label htmlFor="txtUserId">AEPS Blance <span style={{ color: "var(--bs-primary2)" }}>*</span></label>
                                    <input type="number" disabled name="start_date" value={walletData?.aeps_wallet} id="account_no" className="form-control" />
                                </div>
                              
                                <div className="form-group col-sm-12 col-md-4">
                                    <label htmlFor="txtUserId">Amount to be Settled <span style={{ color: "var(--bs-primary2)" }}>*</span></label>
                                    <input type="number" placeholder="Enter 100 - 1,00,000" name="amount" value={initialValue?.amount} id="account_no"  className={`form-control ${amountError ? 'is-invalid' : ''}`}  onChange={handleChangeSet} />
                                    
                                </div>
                                <div className="form-group col-sm-12 col-md-4">
                                    <label htmlFor="txtUserId">Payment Mode <span style={{ color: "var(--bs-primary2)" }}>*</span></label>
                                    <div className="set-head-mate" style={{justifyContent:'flex-start'}}>
                                        <div className="form-check">
                                            <input className="form-check-input" type="radio" name="channel" defaultChecked id="NEFT" onChange={(e) => handleChangeChanel('neft')} />
                                            <label className="form-check-label" htmlFor="NEFT">
                                                NEFT
                                            </label>
                                        </div>
                                        <div className="form-check mr-3 ml-2">
                                            <input className="form-check-input" type="radio" name="channel" id="IMPS" onChange={(e) => handleChangeChanel('imps')} />
                                            <label className="form-check-label" htmlFor="IMPS">
                                                IMPS
                                            </label>
                                        </div>
                                        {/* <div className="form-check mr-3">
                                            <input className="form-check-input" type="radio" name="channel" id="RTGS" onChange={(e) => handleChangeChanel('RT')} />
                                            <label className="form-check-label" htmlFor="RTGS">
                                                RTGS
                                            </label>
                                        </div>
                                        <div className="form-check mr-3">
                                            <input className="form-check-input" type="radio" name="channel" id="FundTransfer" onChange={(e) => handleChangeChanel('FT')} />
                                            <label className="form-check-label" htmlFor="FundTransfer">
                                                Fund Transfer
                                            </label>
                                        </div>
                                        <div className="form-check mr-3">
                                            <input className="form-check-input" type="radio" name="channel" id="CorporateCheques" onChange={(e) => handleChangeChanel('CC')} />
                                            <label className="form-check-label" htmlFor="CorporateCheques">
                                                Corporate Cheques
                                            </label>
                                        </div>
                                        <div className="form-check mr-3">
                                            <input className="form-check-input" type="radio" name="channel" id="DD" onChange={(e) => handleChangeChanel('DD')} />
                                            <label className="form-check-label" htmlFor="DD">
                                                Demand Draft
                                            </label>
                                        </div> */}
                                    </div>

                                </div>
                                <div className="form-group col-sm-12 col-md-4">
                                        <label htmlFor="txtUserId">Bank Name</label>
                                        <input type="text" name="bank_name" value={initialValue.bank_name} className="form-control" readOnly />
                                    </div>
                                    <div className="form-group col-sm-12 col-md-4">
                                        <label htmlFor="txtUserId">Account Holder Name</label>
                                        <input type="text" name="account_holder_name" value={initialValue.account_holder_name} className="form-control" readOnly />
                                    </div>
                                    <div className="form-group col-sm-12 col-md-4">
                                        <label htmlFor="txtUserId">IFSC Code</label>
                                        <input type="text" name="ifsc_code" value={initialValue.ifsc_code} className="form-control" readOnly />
                                    </div>
                                    <div className="form-group col-sm-12 col-md-4">
                                        <label htmlFor="txtUserId">Mobile Number</label>
                                        <input type="text" name="mobile_number" value={initialValue.mobile_number} className="form-control" readOnly />
                                    </div>
                                    <div className="form-group col-sm-12 col-md-4">
                                        <label htmlFor="txtUserId">Account Number</label>
                                        <input type="text" name="account_number" value={initialValue.account_number} className="form-control" readOnly />
                                    </div>
                               
                            </div>
                        </form>
                        
                        </Modal.Body>
      <Modal.Footer>
      {amountError && <div className="error-message">{amountError}</div>}
        <Button variant="secondary" onClick={handleCloseForm}>
          Close
        </Button>
        <Button variant="primary" disabled={!initialValue?.amount || amountError}  onClick={() => {
    setShowForm(false);
    setModalShow(true);
  }}>
          Proceed
        </Button>
      </Modal.Footer>
    </Modal>
                    </div>
                </div>
                {/* <AepsPinModal
                    show={modalShow}
                    onHide={() => setModalShow(false)}
                    handleSumitPayemt={handleSumitPayemt}
                    initialValue={initialValue}
                    selectedItem={selectedItem}
                    toastSuccessMessage={toastSuccessMessage}
                    toastErrorMessage={toastErrorMessage}
                   
                /> */}

                {modalShow && 
                <AepsPinModalNew
                 show={modalShow}
                 setLoading={setLoading}
                 onHide={() => {
                    setModalShow(false);
                    setInitialValue('');
                  }}
                 handleSumitPayemt={handleSumitPayemt}
                 initialValue={initialValue}
                 selectedItem={selectedItem}
                 toastSuccessMessage={toastSuccessMessage}
                 toastErrorMessage={toastErrorMessage}

                /> }
            </div>
            <Modal 
  show={showAddPayoutModal} 
  onHide={() => setShowAddPayoutModal(false)}
  size="lg"
  centered
  className="aeps-payout-modal"
>
  <Modal.Header closeButton>
    <Modal.Title>Add Payout Account</Modal.Title>
  </Modal.Header>
  <Modal.Body>
    <AddAepsPayout
      setLoading={setLoading}
      onSuccess={() => {
        setShowAddPayoutModal(false);
        aepsPayoutList(); 
      }} 
    />
  </Modal.Body>
</Modal>
    
          
        </>
    )
}
export default AepsPayout